document.addEventListener("DOMContentLoaded", function () {
  const tableBody = document.querySelector("#students-table tbody");

  // Fetch data from your MockAPI
  fetch("https://681738fd26a599ae7c39acb9.mockapi.io/students/students")
    .then((response) => response.json())
    .then((students) => {
      students.forEach((student) => {
        const row = document.createElement("tr");

        // Create cells
        row.innerHTML = `
            <td>${student.id}</td>
            <td>${student.name}</td>
            <td>${student.age}</td>
            <td>${student.course}</td>
            <td>${student.skills.join(", ")}</td>
            <td>${student.email}</td>
            <td>${student.isEnrolled ? "–ó–∞—Ä–∞—Ö–æ–≤–∞–Ω–æ" : "–ù–µ –∑–∞—Ä–∞—Ö–æ–≤–∞–Ω–æ"}</td>
            <td>
              <button onclick="editStudent(${student.id})">‚úèÔ∏è</button>
              <button onclick="deleteStudent(${student.id})">üóëÔ∏è</button>
            </td>
          `;

        tableBody.appendChild(row);
      });
    })
    .catch((error) => {
      console.error("–ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç—É–¥–µ–Ω—Ç—ñ–≤:", error);
    });
});

